<template lang="html">
  <div class="">
    <default-head header="线路规划"></default-head>
    <div class="baidu-map-box">
      <div id="baidu-map"></div>
    </div>
    <transition enter-active-class="animated slideInUp" mode="out-in">
      <div v-show="showSlide" class="result-panel-box">
        <div class="result-panel-hd" @click="showPath = !showPath">
          <span class="icon-box">
            <i class="arrow-icon" :class="{'arrow-icon-up': !showPath, 'arrow-icon-down': showPath}"></i>
          </span>
        </div>
        <div v-show="showPath" class="animated" id="result-panel"></div>
      </div>
    </transition>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
export default {
  components: {defaultHead},
  data () {
    return {
      paths: [],
      showPath: true,
      showSlide: false
    }
  },
  computed: {
    mapLocation: function () {
      let mapLocation = JSON.parse(sessionStorage.getItem('mapLocation'))
      sessionStorage.removeItem('mapLocation')
      return mapLocation
    }
  },
  mounted () {
    this.initPage()
  },
  methods: {
    initPage () { // 初始化地图及路线
      minutils.IndicatorOpen('加载中...')
      utils.getPathOfBus('baidu-map', 'result-panel', this.mapLocation, () => {
        this.showPath = true
        minutils.IndicatorClose()
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.baidu-map-box {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
#baidu-map {
  height:100%;
  width: 100%;
}
.result-panel-box {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: @whiteColor;
  .result-panel-hd {
    height: .5rem;
    padding: .06rem 0;
    border-top: 1px solid @lineColor;
    text-align: center;
    .icon-box {
      display: inline-block;
      border: 1px solid @lineColor;
      border-radius: .05rem;
      width: .8rem;
      height: .35rem;
      line-height: .38rem;
    }
    .arrow-icon {
      display: inline-block;
      width: 0;
      height: 0;
      border-left: .15rem solid transparent;
      border-right: .15rem solid transparent;
    }
    .arrow-icon-up {
      border-bottom: .2rem solid @greenBtn;
    }
    .arrow-icon-down {
      border-top: .2rem solid @greenBtn;
    }
  }
}
#result-panel{
  height: 5rem;
  overflow-y: auto;
  border-top: 1px solid @lineColor;
  box-shadow: 0 0 .16rem rgba(0, 0, 0, .1);
  .tranroute-plan-list>td div:first-child {
    margin: 0 !important;
    border-top: none !important;
  }
  h1 {
    display: none;
  }
  h1~div {
    display: none
  }
}
</style>



